import * as React from "react";
import { createStore } from 'redux';
import { Provider } from 'react-redux';

import { Utils } from '../utils/Utils';

import { LoginForm } from "../components/LoginForm";

import { LoginReducers } from "../reducers/login.reducer";

let defultInitState = {
    loginName : "",
    password : "",
    verification : "",
    loginNameError: false ,
    loginNameErrorInfo: "",
    passwordError: false,
    passwordErrorInfo: "",
    verificationError: false,
    verificationErrorInfo: "",
    verificationImgUrl : "src/images/VerifyCode.png"
}

let store = createStore(LoginReducers.LoginReducer, defultInitState);

let locationKey : string;


export default class Login extends React.Component<any, any>{

    componentWillReceiveProps(){

    }

    ComponentDidMOunt(){

    }

    render(){
        if(locationKey !== this.props.location.key){
            store.dispatch({type : "INIT"});
            locationKey = this.props.location.key;
        }

        return (
            <Provider store={store}>
                <div className="content main page login">
                    <div className="row">
                        <div className="form-box">
                            <LoginForm store={store} />
                        </div>
                    </div>
                </div>
            </Provider>
        );
    }
}




